<template>
  <div class="warp">
    <div class="search-tool">
      <input name="zp_name" type="text" class="input" placeholder="请输入商家名称或商圈">
    </div>
    <div class="shop-content">
      <div class="filter container" data-spm="filter">
        <div class="filter-bar row">
          <span class="bar-item cell" @click="filterClick(1)" v-bind:class="{ 'text-accent': activeFilter==1 }">菜系&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
          <span class="bar-item cell" @click="filterClick(2)" v-bind:class="{ 'text-accent': activeFilter==2 }">排序&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
          <span class="bar-item cell" @click="filterClick(3)" v-bind:class="{ 'text-accent': activeFilter==3 }">安全等级&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
        </div>
        <div class="expanding" v-show="expandingShow">
          <div class="sort list with-divider">
            <span class="sort-item item">默认</span>
            <span class="sort-item item">离我最近</span>
            <span class="sort-item item">最新发布</span>
          </div>
        </div>
        <div class="mask" v-show="expandingShow" @click="expandingHide"></div>
      </div>
      <scroller :on-refresh="refresh" :on-infinite="infinite">
        <div class="shop-content-list">
          <div class="shop-content-list-item row" v-for="shop in shopList">
            <div class="icon-part pin">
              <img class="shop-logo" v-bind:alt="shop.zp_name" v-bind:src="shop.mtzp + '?x-oss-process=image/resize,m_lfit,w_80,limit_0/auto-orient,0/sharpen,93/quality,q_100'"
                data-src-checked="true">
            </div>

            <div class="right">
              <div class="title-wrap">
                <span class="title">{{shop.zp_name}}</span>
              </div>
              <div class="security-level">
                <span class="text-gray small">食品安全等级：{{shop.dj}}</span>

              </div>
              <div class="delivery">
                <span class="distance text-gray small">距离{{shop.dis}}</span>
                <span class="pull-right text-gray small">{{shop.sq}}</span>
              </div>
            </div>
          </div>
        </div>
      </scroller>
    </div>

  </div>
</template>

<script>
  import { getShopList } from "../api/api"
  import Vue from 'vue'
  import VueScroller from 'vue-scroller'
  Vue.use(VueScroller)

  export default {
    name: 'Home',
    data() {
      return {
        shopList: [],
        expandingShow: false,
        activeFilter: 0,
      }
    },
    methods: {
      refresh(done) {
        getShopList().then(response => {
          this.shopList = response.data
          done()
        })
      },
      infinite(done) {
        getShopList().then(response => {
          this.shopList = this.shopList.concat(response.data)
          done()
        })
      },
      filterClick(index){
        this.expandingShow = !this.expandingShow
        if(this.expandingShow)
          this.activeFilter = index
        else
          this.activeFilter = 0
      },
      expandingHide(){
        this.expandingShow = false
        this.activeFilter = 0
      }
    },
    components: {

    },
    mounted() {
      getShopList().then(response => {
        this.shopList = response.data
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .search-tool{
    width: 100%;
    top: 0;
    z-index: 99999;
    position: absolute;
    background-color: #fff;
    .input{
      height: 2rem;
    }
  }
  .right {
    text-align: left;
    margin-left: .5rem;
    display: flex;
    flex: 1;
    -webkit-box-flex: 1;
    width: 100%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: start;
    .title{
      font-size: 16px;
      font-weight: 700;
    }
  }

  .shop-content-list {
    padding: .5rem;
    margin-top: 4rem;
    background-color: white;
  }
  .shop-content{
    width: 100%;
    height: 100%;
  }
  .shop-content-list-item{
    padding: 5px 0;
    border-bottom: #e6e6e6 1px solid;
  }
  .filter{
    width: 100%;
    top: 2rem;
    z-index: 99999;
    position: absolute;
    background-color: #fff;
    .filter-bar{
      width: 100%;
      cursor: pointer;
      font-size: .32rem;
      z-index: 2;
      background-color: #fff;
      line-height: 1.5rem;
      .cell{
        display: block;
        line-height: 1.5rem;
      }
    }
    .row{
      margin: 0;
    }
    .mask {
        position: fixed;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.5);
        z-index: 2;
    }
  }
</style>



