import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import favorite from '@/components/favorite.vue'
import platform from '@/components/platform.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        index: 0
      }
    },
    {
      path: '/favorite',
      component: favorite,
      name: 'favorite',
      meta: {
        index: 1
      }
      // hidden: true
    },
    {
      path: '/platform',
      component: platform,
      name: 'platform',
      meta: {
        index: 2
      }
      // hidden: true
    }
  ]
})



